/* MarkdownPreviewer.css */

.markdown-previewer {
  display: flex;
  flex-direction: column;
  justify-content: center; /* Center items vertically */
  align-items: center;
  height: 100vh;
  margin: 60px 0px;
}

.editor textarea {
  width: 700px;
  height: 350px;
  padding: 10px;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 5px;
  background-color: #e0f7fa;
}

.preview {
  margin-top: 20px;
  width: 700px;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  background-color: #fce4ec;
}

.preview img {
  max-width: 100%;
  height: auto;
  margin-top: 10px; /* Add margin to the top of the image */
}


@media (max-width: 1100px) {
  .editor textarea,
  .preview {
    max-width: 500px;
  }
  .markdown-previewer{
    margin:  100px 0px;
  }
}

@media (max-width: 780px) {
  .editor textarea,
  .preview {
    max-width: 300px;
  }
  .markdown-previewer{
    margin:  120px 0px;
  }
}



